/* 컨테이너 스타일 */
.review-list-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-family: Arial, sans-serif;
  color: #333;
}
.container {
  width: 50%;
  margin: 0 auto;
  font-family: Arial, sans-serif;
  color: #333;
}

/* 제목 스타일 */
.title {
  font-size: 30px;
  font-weight: bold;
  text-align: left;
  color: #004F99;
}

/* 테이블 스타일 */
.review-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.review-table th,
.review-table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: center;
  border-right: none; /* 기존 border-right 제거 */
  border-left: none;
}

.review-table td {
  cursor: pointer;
}

.review-table th {
  background-color: rgba(109, 160, 255, 0.2);
  font-weight: bold;
  color: #333;
}

.review-table tbody tr:nth-child(even) {
  background-color: rgba(109, 160, 255, 0.06);
}

.review-table tbody tr:hover {
  background-color: rgba(109, 160, 255, 0.3);
}

/* 페이징 버튼 스타일 */
.pagination {
  display: flex;
  justify-content: center;
  gap: 5px;
}

.pagination button {
  padding: 8px 12px;
  border: 1px solid #ddd;
  background-color: #fff;
  color: #333;
  cursor: pointer;
  border-radius: 4px;
}

.pagination button.active {
  background-color: #007bff;
  color: #fff;
  border-color: #007bff;
}

.pagination button:hover {
  background-color: #f2f2f2;
}

/* 추가 행 스타일 */
.clickableRow {
  cursor: pointer;
}

.clickableRow:hover {
  background-color: #f5f5f5;
}

.disabledRow {
  cursor: not-allowed;
  color: gray;
  pointer-events: none;
}
